:root {
    --gilroy-family: 'Gilroy', sans-serif;
    --gilroy-font-weight-400: 400;
    --gilroy-font-weight-500: 500;
    --gilroy-font-weight-600: 600;
    --gilroy-font-weight-700: 700;
    --gilroy-font-weight-800: 800;

    --general-transition-effect: 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

$mobile: 360px;
